import React, {useEffect} from "react";
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Filler,
    Legend,
} from 'chart.js';
import {Line} from 'react-chartjs-2';
import styled from "styled-components";
import {useUpDataPool} from "../../provider/updata_pool_provicer";
import {BondingText} from "../my_pool/hook/usePool";


ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Filler,
    Legend
);

export const options = {
    // responsive: true,
    plugins: {
        legend: {
            position: 'top' as const,
            display: false,

        },
        title: {
            display: false,
            text: 'Chart.js Line Chart',
        },
        callbacks: {
            label: (tooltipItems: any) => {
                let {raw} = tooltipItems;
                return raw
            },
        },
    },
};

const labels = ['1', '2', '3', '4', '5', '6', '7'];

export const data = {
    labels,
    datasets: [
        {
            fill: true,
            label: '',
            data: [1.3, 1.4, 1.5, 1.6, 1.7, 1.8, 1.9],
            borderColor: 'rgb(53, 162, 235)',
            backgroundColor: 'rgba(53, 162, 235, 0.5)',
            tension: 0.5
        },
    ],
};

export const KLink = () => {

    const {collections, sudo} = useUpDataPool();


    return (
        <WapStyle>
            <h3 className={'tis'}>
                <p>ETH Price Per {collections?.name}</p>
                <p className={'sub_title'}>Displays how your buy price decreases with
                    each {collections?.name} bought.</p>

                <div className="kLine">
                    <Line options={options} data={data}/>
                </div>

                <div className="curve">
                    <h3>Bonding Curve</h3>
                    <div>{BondingText[sudo?.bondingCurve || '']} Curve</div>
                </div>

                <div className="curve">
                    <h3>Swap Fee Rate</h3>
                    <div className={`btn`}>Linear Curve <span>%</span></div>
                </div>


            </h3>
        </WapStyle>
    )
}

