import React, {useEffect, useMemo, useState} from "react";
import styled from "styled-components";
import {Grid} from "@material-ui/core";
import {useForm} from "react-hook-form";
import {LabelInput} from "./widget/label_input";
import {KLink} from "./k_link";
import {useCreaterDataPool} from "../../provider/creater_lp_provider";
import {CURVEEnum} from "./type";
import ethIcon from "../../assets/images/pool_eth.png"
import {range, scan} from "ramda";
import {PoolType} from "../collection/pool/nft_pool_item";
import BigNumber from "bignumber.js";

export const SunderBarPage: React.FC = () => {
    const {register, errors, watch, getValues, setValue} = useForm();
    const {moralisData, poolType, curve, nftidsStateAction} = useCreaterDataPool()
    let [pool] = poolType

    let [moralis, _] = moralisData;
    let [_curve] = curve

    const [deposit, setDeposit] = useState('--')
    const watchFields = watch(["spotPrice", "delta", "amount"]);

    useEffect(() => {
        let watchFields = watch(["spotPrice", "delta", "amount"])
        if (watchFields.spotPrice && watchFields.amount) {
            setDeposit((watchFields.spotPrice * watchFields.amount).toFixed(2))
        } else {
            setDeposit('--')
        }

    }, [watch(["spotPrice", "delta", "amount"])])

    let {spotPrice, delta, bonding, amount} = getValues();

    const option = useMemo(() => {

        let xL = amount ? range(1, parseInt(amount)) : []

        spotPrice = parseFloat(spotPrice)
        if (pool === PoolType.token) {
            return scan((acc, elem) => {
                if (_curve === CURVEEnum.EXPONENTIAL) {
                    return new BigNumber(spotPrice).times(Math.pow(1 + (bonding / 100), elem)).toFixed(2)
                }
                return acc += parseFloat(delta)
            }, spotPrice, xL);
        }
        return scan((acc, elem) => {
            if (_curve === CURVEEnum.EXPONENTIAL) {
                return new BigNumber(spotPrice).times(Math.pow(1 - (bonding / 100), elem)).toFixed(2)
            }
            return acc -= parseFloat(delta)
        }, spotPrice, xL);
    }, [_curve, pool, spotPrice, delta, bonding, amount]);

    const total = useMemo(() => {
        let t = new BigNumber(0);

        option.forEach((ev) => {
            t = t.plus(ev);
        })
        return t.toFixed(4);
    }, [option])

    useEffect(() => {
        let ids = nftidsStateAction[0];
        if (ids.length) {
            setValue('amount', ids.length)
        }
    }, [nftidsStateAction])

    return (
        <WapStyle>
            <CellBottomStyle container>

                <Grid item xs={12} sm={12} md={6}>
                    <div className="pt header_title">
                        <h3>Select Token</h3>
                        <h4>
                            <img src={ethIcon} alt=""/> <span>ETH</span>
                        </h4>
                    </div>
                    <div className="pt">
                        <LabelInput errors={errors}
                                    inputRef={register({
                                        required: ''
                                    })}
                                    tis={'----'}
                                    lastTis={`Floor Price: ${watchFields.spotPrice || '--'} ETH`}
                                    label={'Start Price'}
                                    name={'spotPrice'}
                                    placeholder={'0.00'}
                                    unit={"ETH"}
                        />
                    </div>


                    <div className="pt">
                        {
                            _curve == CURVEEnum.LINEAR ? <>
                                <LabelInput errors={errors}
                                            inputRef={register({
                                                required: ''
                                            })}
                                            tis={'----'}
                                            label={'Delta'}
                                            name={'delta'}
                                            placeholder={'0.00'}
                                            unit={"ETH"}
                                />
                            </> : <>
                                <LabelInput errors={errors}
                                            inputRef={register({
                                                required: ''
                                            })}
                                            tis={'----'}
                                            label={'Bonding Curve'}
                                            name={'bonding'}
                                            placeholder={'0.00'}
                                            unit={"%"}
                                />
                            </>
                        }

                    </div>
                    <div className=" pt assets">
                        <h3>Asset Amounts</h3>
                        <h4>Set how many tokens you deposit into the pool.</h4>
                    </div>

                    <div className="pt">
                        <LabelInput errors={errors}
                                    inputRef={register({
                                        required: 'amount'
                                    })}
                                    label={'Amount'}
                                    name={'amount'}
                                    placeholder={'0'}
                        >
                            <div className="btn_max">
                                {moralis.symbol}
                            </div>
                        </LabelInput>
                        {
                            pool === PoolType.trade || pool === PoolType.token &&
                            <p className='bottom_msg'>You will need to deposit {total} ETH total.</p>
                        }
                        {
                            pool === PoolType.nft &&
                            <p className='bottom_msg'>You will receive {total} ETH total.</p>
                        }

                    </div>
                </Grid>
                <Grid item xs={12} sm={12} md={6}>
                    <KLink option={option} watch={watch(["spotPrice", "delta", "amount"])} getValues={getValues}/>
                </Grid>
            </CellBottomStyle>

        </WapStyle>
    );
}

