import React from "react";
import styled from "styled-components";
import Roy from "../../../assets/images/question-mark.svg"
import indexEth from "../../../assets/images/index_eth.png"
import {getBalanceNumber} from "../../../utils/formatBalance";
import {useShoppingCart} from "../../../provider/shopping_cart_provider";
import {useCollection} from "../../../provider/collection_provicer";


type NftSellItemProps = {
    data: {
        id: string,
        uqid: string,
        price: string;
        nftId?: string | number,
        delta?: string,
        imgUrl?: string,
        symbol?: string,
    },
    onClick: Function
}

export const NftSellItem: React.FC<NftSellItemProps> = ({data, onClick}) => {
    let {uqid, price} = data;

    const {nftMeta} = useCollection();
    const {addShopping, deleteShopping, nftMateMap} = useShoppingCart()
    return (
        <NftBuyItemStyle className={`${nftMateMap[uqid] && 'ac'}`} onClick={() => {
            if (!data.price) {
                return;
            }

            if (nftMateMap[uqid]) {
                deleteShopping(uqid)
                onClick(false)
                return;
            }
            addShopping({...nftMeta[uqid], isSell: true, ownedId: data.id, averagePrice: price})
            onClick(true)

        }}
        >
            <div className="top">

                <img src={data.imgUrl || Roy} alt="" className="nft_img"/>
            </div>
            <div className="bottom">
                <p className={'nftId_'}>#{data.nftId}</p>
                <p className={'nftId'}>{data.symbol}</p>

                {!data.price && <div className="bar_b">
                    No offer
                </div>}
                {
                    data.price && <div className="price">
                        <h3>Price</h3>
                        <h4><img src={indexEth}
                                 alt=""/> {getBalanceNumber(data.price, 5)}
                        </h4>
                    </div>
                }

            </div>


        </NftBuyItemStyle>
    );
}

const NftBuyItemStyle = styled.div`
  background: #353840;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  border: 1px solid transparent;
  width: 180px;


  &.ac {
    border: 1px solid #fff;
  }

  &:hover {
    background-color: #5A5A64;
  }


  .bottom {
    padding: 5px 10px;
    box-sizing: border-box;

    .bar_b {
      margin-top: 5px;
      padding: 5px 0;
      background-color: #404040;
      border-radius: 100px;
      text-align: center;
      color: white;

    }

    .price {
      .lastSale {
        display: flex;
        align-items: center;
        font-style: normal;
        font-weight: 400;
        font-size: 11px;
        line-height: 11px;
        margin-top: 10px;
        letter-spacing: -0.14em;
        color: #A0A0AA;

        h4 {
          display: flex;
          align-items: center;
          color: #A0A0AA;
          font-size: 11px;

          img {
            display: block;
            width: 10px;
          }
        }
      }

    }

    .price {
      margin: 5px 0 0px 0;

      h3 {
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 12px;
        align-items: center;
        letter-spacing: -0.14em;
        color: #FFFFFF;
        margin-bottom: 5px;
      }

      h4 {
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 20px;
        /* identical to box height */

        display: flex;
        align-items: center;
        letter-spacing: -0.14em;
        color: #FFFFFF;

        img {
          display: block;
          width: 20px;
          margin-right: 5px;
        }
      }
    }

    .nftId {
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 14px;
      display: flex;
      align-items: center;
      letter-spacing: -0.1em;

      color: #FFFFFF;
    }

    .nftId_ {
      font-size: 8px;
      color: #A0A0AA;
      overflow: hidden; //超出的文本隐藏
      text-overflow: ellipsis; //溢出用省略号显示
      white-space: nowrap; //溢出不换行
    }
  }

  .top {
    position: relative;

    .addr {
      display: block;
      width: 20px;
      border-radius: 40px;
      position: absolute;
      top: 10px;
      left: 10px;
    }

    .nft_img {
      display: block;
      width: 180px;
      height: 160px;
      margin: 0 auto;
    }
  }

`
