import {useStore} from "effector-react";
import {$getMoralisFileData} from "../../inventory/store";
import React, {useCallback, useEffect, useMemo, useState} from "react";
import {MoralisData} from "../../../model/moralis_data";
import {useParams} from "react-router-dom";
import {Grid} from "@material-ui/core";
import Roy from "../../../assets/images/question-mark.svg";
import {NaData} from "../index";
import {useWallet} from "use-wallet";
import {MyPoolData} from "../../../model/my_pool_data";
import {PoolType} from "../pool/nft_pool_item";
import BigNumber from "bignumber.js";
import {useCollection} from "../../../provider/collection_provicer";

import {SellPoolMap, SellQty} from "../type";
import {sort} from "ramda";
import {NftSellItem} from "../widget/nft_sell_item";
import {CURVE} from "../../../web3/token.config";
import {useShoppingCart} from "../../../provider/shopping_cart_provider";
import {NftMetadata} from "../../../model/nft_metadata";
import {useWeb3} from "../../../provider/web3_provider";
import {Contract} from "web3-eth-contract";
import erc721 from "../../../web3/abi/erc721";
import {useAllowance} from "../../../utils/token_server";
import {ApproveButton} from "../../../widget/base/approve_button";
import {sequenceFn} from "../../../utils/sequence";

type SellProps = {}


export const SellWidget: React.FC<SellProps> = () => {
    let {id} = useParams<{ id: string, type: string }>();
    const {sudo} = useCollection();
    const [poolMap, setPoolMap] = useState<SellPoolMap>(sudo);

    const {account} = useWallet();
    const data = useStore($getMoralisFileData)
    const {collectionsMap, nftMateMap} = useShoppingCart()


    const listNft = useMemo(() => {
        if (!data?.result) return [];

        return data?.result?.filter(
            (ev: MoralisData) => ev.token_address.toLowerCase() === id.toLowerCase(),
        )
    }, [data, id])


    const handelUpDataSpotPrice = useCallback((key: string, isAdd: boolean, poolMap: SellPoolMap) => {
        let item: SellQty = poolMap[key];
        if (!item) {
            return
        }
        let qty;

        if (isAdd) {
            qty = item.qty ? item.qty += 1 : 1;
        } else {
            if (item.qty)
                qty = item.qty -= 1;
        }
        item.qty = qty;
        let newMap = {...poolMap, [key]: item}
        setPoolMap(newMap)
    }, [poolMap])

    const pools: any = useMemo(() => {
        let pools: SellPoolMap = {}
        for (const key in poolMap) {
            let item = poolMap[key];
            let eType = parseInt(item.type)
            if (eType === PoolType.trade || eType === PoolType.token) {
                let isAc = new BigNumber(item.ethBalance).gt(0)
                if (isAc) {
                    pools[key] = item
                }
            }
        }
        let list = Object.values(pools);

        return sort((a: SellQty, b: SellQty) => {
            let aSpotPrice = a.spotPrice;
            let aQty = collectionsMap[a.id.toLowerCase()]?.length;
            let aFee = new BigNumber(a.fee).div(1e20).toFixed()
            aFee = aFee === '0' ? '0.05' : aFee

            let bSpotPrice = b.spotPrice;
            let bQty = collectionsMap[b.id.toLowerCase()]?.length;
            let bFee = new BigNumber(b.fee).div(1e20).toFixed()
            bFee = bFee === '0' ? '0.05' : bFee

            let aSequenceFn = sequenceFn(new BigNumber(aSpotPrice), a.delta, a.bondingCurve, aQty || 0);


            let bSequenceFn = sequenceFn(new BigNumber(bSpotPrice), b.delta, b.bondingCurve, bQty || 0);
            let aV = aSequenceFn
                .minus(aSequenceFn.times(aFee));
            let bV = bSequenceFn
                .minus(bSequenceFn.times(bFee));
            return bV.minus(aV).toNumber()
        }, list)
    }, [collectionsMap])

    const currentPaire: MyPoolData = useMemo(() => {
        let item: SellQty = pools[0]
        if (!item) {
            return item
        }
        let aQty = collectionsMap[item.id.toLowerCase()]?.length;

        let sequence = sequenceFn(new BigNumber(item.spotPrice), item.delta, item.bondingCurve, aQty || 0)
        let fee = new BigNumber(item.fee).div(1e20).toFixed();
        fee = fee === '0' ? '0.05' : fee

        let price = sequence
            .minus(sequence.times(fee))
            .toFixed(0)
        item.averagePrice = price;
        return item
    }, [collectionsMap, poolMap])

    useEffect(() => {
        if (!Object.keys(poolMap).length) {
            setPoolMap(sudo)
        }
    }, [sudo])



    return (
        <>
            {
                listNft.length === 0 && <NaData>empty</NaData>
            }

            <Grid container spacing={4}>
                {
                    listNft.map((ev: MoralisData) => {
                        let metadata = JSON.parse(ev.metadata)
                        if (!metadata) {
                            metadata = {
                                name: ev.name,
                                image: Roy
                            }
                        }
                        let uqid = `${account}_${ev.token_id}`
                        let current: MyPoolData = currentPaire;
                        let nftMate: NftMetadata = nftMateMap[uqid];

                        return (
                            <Grid item key={`sell_${uqid}`}>
                                <NftSellItem
                                    data={{
                                        id: current?.id,
                                        imgUrl: metadata.image || metadata.image_url,
                                        nftId: ev.token_id,
                                        symbol: metadata.name || ev.name,
                                        price: nftMate?.averagePrice || current?.averagePrice,
                                        uqid,
                                    }}
                                    onClick={(isAdd: boolean) => {
                                        handelUpDataSpotPrice(currentPaire?.id, isAdd, poolMap)
                                    }}
                                />
                            </Grid>
                        );
                    })
                }
            </Grid>
        </>
    );
}
